$( ".works-card__slider" ).each(function(index) {

    var $status = $(this).find(".pagingInfo");

    $(this).on('init reInit afterChange', function(event, slick, currentSlide, nextSlide) {
        var i = (currentSlide ? currentSlide : 0) + 1
        if (i < 10) {
            $status.html(`<span>0${i}</span> <span>${slick.slideCount}</span>`)
        } else {
            $status.html(`<span>${i}</span> <span>${slick.slideCount}</span>`)
        }
    });

    $('.big-slider', $(this)).slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        fade: true,
        asNavFor: $(this).find('.small-slider__wrap')
    });
    $('.small-slider__wrap', $(this)).slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        dots: true,

        centerMode: true,
        focusOnSelect: true,
        asNavFor: $(this).find('.big-slider'),
        responsive: [
            {
                breakpoint: 993,
                settings: {
                    slidesToShow: 2,
                }
            },
        ]
    });

});

