$(document).ready(function() {

    //Menu
    $('.burger').click(function () {
        $('.header__right').addClass('header__right--active')
        $('.menu-bg').addClass('menu-bg--active')
        $('body').addClass('fixed-body')
    })

    $('.close-menu').click(function () {
        $('.header__right').removeClass('header__right--active')
        $('.menu-bg').removeClass('menu-bg--active')
        $('body').removeClass('fixed-body')
    })

    //Filter
    $('.works__filter').click(function () {
        $('.works__top').addClass('works__top--active')
        $('.filter-bg').addClass('filter-bg--active')
        $('body').addClass('fixed-body')
    })

    $('.close-menu').click(function () {
        $('.works__top').removeClass('works__top--active')
        $('.filter-bg').removeClass('filter-bg--active')
        $('body').removeClass('fixed-body')
    })

    //Select
    $('.select__list-default').hover(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list .select__check').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list .select__check").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list .select__check')
    let selectWrap = $('.select__wrap')

    $(document).mouseover(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    // Tabs
    $(".quiz-pagination__btn").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".quiz-pagination__btn").removeClass("pagination-active")
        $(this).addClass("pagination-active")
        if ($('.quiz-pagination__btn:first-child').hasClass('pagination-active')) {
            $('.quiz-btn__prev').removeClass('quiz-btn__prev--active')
        }

        $(this).parents(".quiz__inner").find(".quiz__content").removeClass("quiz__content--active")
        $(this).parents(".quiz__inner").find(`.quiz__content[data-content-path="${path}"]`).addClass("quiz__content--active")
    })

    $('.quiz-pagination__btn:not(:first-child)').click(function () {
        $('.quiz-btn__prev').addClass('quiz-btn__prev--active')
    })

    $('.quiz-btn__next').click(function(e) {
        $('.quiz-btn__prev').addClass('quiz-btn__prev--active')

        var $current = $('.quiz__content--active');
        var $current2 = $('.pagination-active');

        $current.removeClass('quiz__content--active');
        $current.next('.quiz__content').addClass('quiz__content--active');
        if (!$current.next('.quiz__content').get(0)) {
            $('.quiz__wrap').remove()
            $('.quiz-form').addClass('quiz-form--active')
        }

        $current2.removeClass('pagination-active');
        $current2.next('.quiz-pagination__btn').addClass('pagination-active');

    });

    $('.quiz-btn__prev').click(function () {

        var $current = $('.quiz__content--active');
        var $current2 = $('.pagination-active');

        $current.removeClass('quiz__content--active');
        $current.prev('.quiz__content').addClass('quiz__content--active');
        if ( $('.quiz__content').first().hasClass('quiz__content--active') ) {
            $(this).removeClass('quiz-btn__prev--active')
        }

        $current2.removeClass('pagination-active');
        $current2.prev('.quiz-pagination__btn').addClass('pagination-active');
    })


})

// modal
$("[data-modal]").click(function () {
    let modalId = $(this).attr("data-modal")

    openModal(modalId)
})

function openModal(modalId){
    $(`#${modalId}`).addClass("modal--active")
    $("body").addClass("fixed-body")
}

$(".modal").click(function () {
    closeModal()
})

$("._close-modal").click(function () {
    closeModal()
})

$(".modal__dialog").click(function (e) {
    e.stopPropagation()
})


function closeModal() {
    $('.modal').removeClass("modal--active")
    $("body").removeClass("fixed-body")
}


// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}